$border-color: #e8e8e8;
$theme-color: #2196f3;
$grey-color: #6e6a6a;

@mixin wrapper {
    width: 100%;
    height: 100%;
}

.list-cell {
    background-color:#fff;
    box-sizing:border-box;
    color:inherit;
    min-height:48px;
    display:block;
    overflow:hidden;
    position:relative;
    text-decoration:none;
}
.list-cell-allow-right{
    top:50%;
    right:20px;
    position: absolute;
    transform: translateY(-50%);
    font-size: 20px;
    color: $theme-color;
    display: block;
}
.list-cell img {
    vertical-align:middle;
}
.list-cell:first-child .list-cell-wrapper {
    background-origin:border-box;
}

.list-cell-wrapper {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    font-size: 16px;
    line-height: 1;
    min-height: inherit;
    overflow: hidden;
    padding: 0 10px;
    width: 100%;
    padding-top: 15px;
    padding-bottom: 15px;
    border-bottom: 1px solid #efefef;
}
.list-cell-mask {}
.list-cell-mask::after {
    background-color:#000;
    content:" ";
    opacity:0;
    top:0;
    right:0;
    bottom:0;
    left:0;
    position:absolute;
}
.list-cell-mask:active::after {
    opacity:.1;
}
.list-cell-text {
    vertical-align: middle;
}
.list-cell-label {
    color: #888;
    display: block;
    font-size: 12px;
    margin-top: 10px;
}
.list-cell-image {
    width: 90px;
    height: 60px;
    overflow: hidden;
    margin-right: 10px;
    background-size: cover;
    background-repeat: no-repeat;
    // background-image: url(../img/wrj/demo1.jpg);
}
.list-cell-title {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}
.list-cell-value {
    color: #888;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.list-cell-value.is-link {
    margin-right:24px;
}
.list-cell-left {
    position: absolute;
    height: 100%;
    left: 0;
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
.list-cell-right {
    position: absolute;
    height: 100%;
    right: 0;
    top: 0;
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
