/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
html{
  font-size: 62.5%;

}
html,body{
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
}
//XX管理公共列表页及详情页样式
.common-container{
  padding-bottom: 48px;
  .common-list{
    display: flex;
    background: #ffffff;
    padding: 5px 20px;
    border-bottom: 1px dashed $border-color;
    //证书及无人机等图片样式
    img{
      width: 80px;
      height: 80px;
      padding: 0 20px;
    }
    .common-info{
      flex: 1;
      font-size: 14px;
      //证书及无人机两行描述文字
      .common-info-text{
        padding: 10px 0px 10px 20px;

      }
      //证书及无人机单行描述文字
      .common-single-text{
        padding: 10px 0px 10px 20px;
        line-height: 60px;

      }
    }
    //证书及无人机右侧图标
    .common-right{
      color: $theme-color;
      line-height: 80px;
      font-size: 24px;
      width: 20px;

    }

  }
  //底部固定按钮,用于新增
  .common-footer {
    position: fixed;
    bottom: 0;
    height: 48px;
    width: 100%;
    background-color: #fff;
    border-top: 1px solid $theme-color;
    text-align: center;
    .common-footer-btn {
      width: 100%;
      height: 100%;
      line-height: 48px;
      background: $theme-color;
      display: inline-block;
      text-align: center;
      color: #fff;

    }

  }
}
//填写表单信息页及详情页样式
.group {
  display: flex;
  background-color: #fff;
  border-width: 0 0 1px 0;
  border-style: solid;
  border-color: $border-color;
  label {
    vertical-align: middle;
    width: 30%;
    line-height: 48px;
    padding-left: 8px;
    text-align: center;
  }
  .selection-list {
    display: flex;
    flex: 1;
    flex-flow: column;
    width: 70%;
    .selection {
      width: 100%;
      height: 48px;
      position: relative;
      border-bottom: 1px solid $border-color;
      &:last-child {
        border-bottom: 0 none;
      }
      .input-box {
        padding-right: 20px;
        height: 48px;
        line-height: 48px;
        color: $theme-color;
        padding-left: 20px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        & > * {
          color: $theme-color;
          border: 0 none;
        }
        input {
          height: 40px;
          line-height: 40px;
          font-size: 14px;
        }
        //去除浏览器自动填充颜色
        input:-webkit-autofill {
          -webkit-box-shadow: 0 0 0 1000px white inset !important;
        }
        .iconfont{
            width: 20px;
            height: 20px;
            display: inline-block;
            position: absolute;
            top: 50%;
            right: 10px;
            transform: translateY(-50%);
            text-align: center;
            line-height: 20px;
            font-size: 20px;
            color: $theme-color;
        }
      }

    }
  }
}
//红色的按钮，用于删除、举报
.common-footer-red{
  text-align: center;
  background: #ffffff;
  .footer-red-btn{
    display: inline-block;
    width: 80%;
    margin-top: 20px;
    margin-bottom: 20px;
    text-align: center;
    border-radius: 10px;
    padding: 12px 10px;
    background: #e51c23;
    color: white;
  }
}




